import React, { Component } from 'react'
import { withRouter } from 'react-router-dom';
import { Card, Row, Col, Input, Button, message, Popconfirm, Badge, Tooltip, Select, Table } from 'antd'
import MainModal from '../../../../../common/components/MainModal'
import UploadModel from './UploadModel'
import { hostUrl } from '../../../../../env'
import {postAction} from '../../../../../axios';
import '../style.less';
const Option = Select.Option;

class Personal extends Component {
    constructor(props){
        super(props);
        let columns = [{
            title: '序号',
            dataIndex: 'index',
            width: '6%'
        }, {
            title: '学号',
            dataIndex: 'studentId',
            width: '10%',
            render: (text) => {
            	return (
            		<span title={text}>{text}</span>
            	)
            }
        }, {
            title: '一卡通号',
            dataIndex: 'studentNo',
            width: '10%',
            render: (text) => {
                return (
                    <span title={text}>{text}</span>
                )
            }
        }, {
            title: '姓名',
            dataIndex: 'name',
            width: '10%',
            render: (text) => {
            	return (
            		<span title={text}>{text}</span>
            	)
            }
        }, {
            title: '性别',
            dataIndex: 'gender',
            width: '6%'
        }, {
            title: '院系',
            dataIndex: 'collegeName',
            width: '15%',
            render: (text) => {
            	return (
            		<span title={text}>{text}</span>
            	)
            }
        }, {
            title: '不预警模块',
            dataIndex: 'alarmType',
            width: '15%',
            render: (text) => {
            	return (
            		<span title={text}>{text}</span>
            	)
            }
        }, {
            title: '备注',
            dataIndex: 'remark',
            width: '12%',
            render: (text) => {
            	return (
            		<span title={text}>{text}</span>
            	)
            }
        }, {
            title: '状态',
            dataIndex: 'status',
            width: '10%',
            render: (text, record)=>{
                let badge;
                if(text==='生效'){
                    badge = <Badge status="processing" />
                } else if(text==='未生效'){
                    badge = <Badge status="success" />
                } else {
                    badge = <Badge status="default" />
                }
                return <span>{badge}{text}</span>;
            }
        }, {
            title: '操作',
            dataIndex: 'handle',
            width: '15%',
            render: (text, record)=>(
                <div>
                    <a style={{color:'#309cff',marginRight:10}} onClick={()=>{
                        this.props.history.push('/mainframe/systemsetup/earlywarningwhitelist/personal/addoredit?id='+record.id)
                    }} href={'javascript:void(0)'}>编辑</a>
                    <Popconfirm title="确认删除?" onConfirm={() => this.deleteAction(record.id)}>
                        <a href="javascript:;" style={{color:'#8d99b0'}}>删除</a>
                    </Popconfirm>
                </div>
            ),
            onCell:()=>{
                return {
                    onClick:(e)=>e.stopPropagation()
                }
            }
        }];
        this.state = ({
            columns,
            pageNum: 1,
            pageSize: 10,
            total:0,
            dataLists:[],
            isEdit: false,
            editTag: {},
            showDetail:false,
            detailInfo:{},
            isImport: false,
            //搜索参数
            studentNo: undefined,
            gender: undefined,
            majorCode: undefined,
            collegeCode: undefined,
            classCode: undefined,
            collegeLists: [],
            majorLists: [],
            classLists: [],
            genderLists: ['男','女']
        })
    }
    componentWillMount(){
        this.getCollegeLists();
        this.getData();
    }
    //学院
    getCollegeLists(){
        postAction('/bigdata/school/listCollege').then(res=>{
            if(res.success){
                this.setState({ collegeLists: res.obj });
            }
        })
    }
    //专业
    getMajorLists(collegeCode){
        if (collegeCode === null) {
            this.setState({ majorLists: [] });
            return;
        }
        let data = {collegeCode}
        postAction('/bigdata/school/listMajorByCollege',data).then(res=>{
            if(res.success){
                this.setState({ majorLists:res.obj });
            }
        })
    }
    changeFilter(value, teamType) {
        switch (teamType) {
            case 1:
                this.setState({ collegeCode: value, majorCode: undefined },()=>this.getMajorLists(value)); break;
            case 2:
                this.setState({ majorCode: value }); break;
            case 3:
                this.setState({ gender: value }); break;
            case 4:
                this.setState({ studentNo: value }); break;
            default:
                break;
        }
    }
    getData = () => {
        let { collegeCode, majorCode, gender, studentNo,pageNum,pageSize } = this.state;
        let data={pageInfo: {pageNum:pageNum,pageSize:pageSize}};
        postAction("/bigdata/alarmWhitelist/getPageData", Object.assign(data,{
            collegeCode, majorCode, gender, studentNo
        })).then(
            (res) => {
                if (res.success) {
                    this.setState({
                        total: res.obj.total,
                        dataLists: res.obj.list || []
                    })
                } else {
                    message.warn(res.obj);
                }
            }
        )
    }
    deleteAction = (id)=> {
        let data = { id };
        postAction("/bigdata/alarmWhitelist/delById", data).then(
            (res) => {
                if (res.success) {
                    message.success("删除成功！");
                    this.getData();
                } else {
                    message.warn(res.obj);
                }
            }
        )
    }
    changePage = (pagination)=> {
        this.setState({pageNum:pagination.current,pageSize:pagination.pageSize},()=>{
           this.getData();
        })
    }
    rowClick = (record)=> {
        let data = {id:record.id}
        postAction("/bigdata/alarmWhitelist/findById", data).then(
            (res) => {
                if (res.success) {
                    this.setState({
                        showDetail: true,
                        detailInfo:Object.assign({},res.obj,record)
                    })
                } else {
                    message.warn(res.obj);
                }
            }
        )
    }
    importAction = (data)=> {
        postAction(`/bigdata/alarmWhitelist/uploadWhitelistData?filePath=${data}`,{}).then(
            (res) => {
                if (res.success) {
                    message.success('保存成功！');
                    this.setState({isImport:false},()=>{
                        this.getData();
                    })
                } else {
                    message.warn(res.obj);
                }
            }
        )
    }
    searchAction = ()=> {
        this.getData();
    }
    render() {
        const dataSource = this.state.dataLists || [];
        dataSource.forEach((item,index)=>{
            dataSource[index].key = index;
            dataSource[index].index = index + 1;
        })
        return (
            <Card 
                className={this.props.className}
                title={(
                    <Row>
                        
                        <Col span={24} style={{textAlign:'left',paddingTop:5}}>
                            <Tooltip placement="top" title="添加" overlayClassName={'common-tooltip'}>
                                <span style={{marginRight:12,fontSize:20,color:'#1890ff'}} 
                                    className="iconfont icon-btn icon-tianjiachengyuan" onClick={()=>{
                                        this.props.history.push('/mainframe/systemsetup/earlywarningwhitelist/personal/addoredit')
                                    }}></span>
                            </Tooltip>
                            <Tooltip placement="top" title="导入" overlayClassName={'common-tooltip'} onClick={()=>this.setState({isImport:true})}>
                                <span style={{marginRight:12,color:'#1890ff'}} className="iconfont icon-btn icon-wenjiandaoru"></span>
                            </Tooltip>
                            <Tooltip placement="top" title="下载" overlayClassName={'common-tooltip'}>
                                <span style={{fontSize:20,color:'#1890ff'}} className="iconfont icon-btn icon-msnui-download" onClick={()=>{
                                    window.location.href = hostUrl+'/bigdata/alarmWhitelist/downloadWhitelistTemplate';
                                }}></span>
                            </Tooltip>
                        </Col>
                    </Row>
                )}>
                {this.state.isImport?(
                    <UploadModel
                        visible={this.state.isImport}
                        action={'/bigdata/alarmWhitelist/uploadWhitelistFile'}
                        onCancel={()=>this.setState({isImport:false})}
                        onOk={this.importAction}
                    />
                ):null}
                <div style={{marginBottom:24}}>
                    筛选条件：
                    <Select className="mr-10" style={{width:160,marginRight:10}}
                        placeholder="选择学院"
                        value={this.state.collegeCode}
                        onChange={(value) => { this.changeFilter(value, 1) }}>
                       {this.state.collegeLists.map((item, index) => <Option key={index} value={item.collegeCode}>{item.collegeName}</Option>)}
                    </Select>
                    <Select className="mr-10" style={{width:160,marginRight:10}}
                        placeholder="选择专业"
                        value={this.state.majorCode}
                        onChange={(value) => { this.changeFilter(value, 2) }}>
                        {this.state.majorLists.map((item, index) => <Option key={index} value={item.majorCode}>{item.majorName}</Option>)}
                    </Select>
                    {/* <Select className="mr-10" style={{width:160,marginRight:10}}
                        placeholder="选择班级"
                        value={this.state.majorCode}
                        onChange={(value) => { this.changeFilter(value, 5) }}>
                        {this.state.majorLists.map((item, index) => <Option key={index} value={item.majorCode}>{item.majorName}</Option>)}
                    </Select> */}
                    <Select className="mr-10" style={{width:160,marginRight:10}}
                        placeholder="性别"
                        value={this.state.gender}
                        onChange={(value) => { this.changeFilter(value, 3) }}>
                        {this.state.genderLists.map((item, index) => <Option key={index} value={item}>{item}</Option>)}
                    </Select>
                    <Input placeholder="请输入学生姓名/学号" 
                        style={{width:172,marginRight:10}}
                        value={this.state.studentNo} 
                        onChange={(e) => { this.changeFilter(e.target.value, 4) }} />
                    <Button icon="search" type="primary" onClick={this.searchAction}>搜 索</Button>
                </div>
                <Table dataSource={dataSource} 
                    rowClassName="clickRow"
                    columns={this.state.columns}
                    pagination={{
                        size: "small",
                        current:this.state.pageNum,
                        total:this.state.total,
                        showQuickJumper: true,
                        showTotal: total => `共 ${total} 条数据`
                    }}
                    size="middle"
                    bordered
                    onChange={this.changePage}
                    onRow={(record, index) => {
                        return {
                            onClick: () => this.rowClick(record, index),// 点击行
                        };
                    }} />
                {this.state.showDetail?(
                    <MainModal
                        title={'查看详情'}
                        visible={this.state.showDetail}
                        onCancel={()=>this.setState({showDetail:false})}
                        width={656}
                        noFooter={true}
                    >
                        <div className="ewc-detailModel">
                            <Row><Col span={8}>姓名/学号</Col><Col span={16}>{this.state.detailInfo.studentId+'，'+this.state.detailInfo.name}</Col></Row>
                            <Row><Col span={8}>所属院系</Col><Col span={16}>{this.state.detailInfo.collegeName}</Col></Row>
                            <Row><Col span={8}>性别</Col><Col span={16}>{this.state.detailInfo.gender}</Col></Row>
                            <Row><Col span={8}>不预警时间</Col><Col span={16}>{this.state.detailInfo.startDate + '～' + this.state.detailInfo.endDate}</Col></Row>
                            <Row><Col span={8}>不预警模块</Col><Col span={16}>{this.state.detailInfo.alarmType}</Col></Row>
                            <Row style={{height:102}}>
                                <Col span={8}><Row type="flex" align="middle" justify="center">备注</Row></Col>
                                <Col span={16}>
                                    <Row type="flex" align="middle" style={{overflowY:'auto'}}>
                                        {this.state.detailInfo.remark}
                                    </Row>
                                </Col>
                            </Row>
                        </div>
                    </MainModal>
                ):null}
            </Card>
        )
    }
}

export default withRouter(Personal)


