import React, { Component } from 'react'
import { withRouter } from 'react-router-dom'
import { Card, Row, Col, Button, message, Popconfirm, Select, Table } from 'antd'
import MainModal from '../../../../../common/components/MainModal'
import {postAction} from '../../../../../axios';
import '../style.less';
const Option = Select.Option;

class Holiday extends Component {
    constructor(props){
        super(props);
        let columns = [{
            title: '序号',
            dataIndex: 'index',
            width: '6%'
        }, {
            title: '预警策略名称',
            dataIndex: 'strategyName',
            width: '20%',
            render: (text) => {
            	return (
            		<span title={text}>{text}</span>
            	)
            }
        }, {
            title: '不预警院系',
            dataIndex: 'strategyTargetCollege',
            width: '20%',
            render: (text) => {
            	return (
            		<span title={text}>{text}</span>
            	)
            }
        }, {
            title: '备注',
            dataIndex: 'remark',
            render: (text) => {
            	return (
            		<span title={text}>{text}</span>
            	)
            }
        }, {
            title: '更新时间',
            dataIndex: 'updateDate',
            width: '15%'
        }, {
            title: '操作',
            dataIndex: 'handle',
            width: '15%',
            render: (text, record)=>(
                <div>
                    <a style={{color:'#309cff',marginRight:10}} onClick={()=>{
                        this.props.history.push('/mainframe/systemsetup/earlywarningwhitelist/holiday/addoredit?id='+record.id)
                    }} href={'javascript:void(0)'}>编辑</a>
                    <Popconfirm title="确认删除?" onConfirm={() => this.deleteAction(record.id)}>
                        <a href="javascript:;" style={{color:'#8d99b0'}}>删除</a>
                    </Popconfirm>
                </div>
            ),
            onCell:()=>{
                return {
                    onClick:(e)=>e.stopPropagation()
                }
            }
        }];
        this.state = ({
            columns,
            pageNum: 1,
            pageSize: 10,
            total:0,
            dataLists:[],
            isEdit: false,
            editTag: {},
            showDetail:false,
            detailInfo:{},
            //搜索参数
            collegeCode: undefined,
            collegeLists: []
        })
    }
    componentWillMount(){
        this.getCollegeLists();
        this.getData();
    }
    //获取学院列表
    getCollegeLists(){
        postAction('/bigdata/school/listCollege').then(res=>{
            if(res.success){
                this.setState({ collegeLists: res.obj });
            }
        })
    }
    //获取数据列表
    getData = () => {
        let data={pageInfo: {pageNum:this.state.pageNum,pageSize:this.state.pageSize},strategyTarget:this.state.collegeCode}
        postAction("/bigdata/alarmHolidayWhitelist/getPageData", data).then(
            (res) => {
                if (res.success) {
                    this.setState({
                        total: res.obj.total,
                        dataLists: res.obj.list || []
                    })
                } else {
                    message.warn(res.obj);
                }
            }
        )
    }
    deleteAction = (id)=> {
        let data = { id };
        postAction("/bigdata/alarmHolidayWhitelist/delById", data).then(
            (res) => {
                if (res.success) {
                    message.success("删除成功！");
                    this.getData();
                } else {
                    message.warn(res.obj);
                }
            }
        )
    }
    changePage = (pagination)=> {
        this.setState({pageNum:pagination.current,pageSize:pagination.pageSize},()=>{
            this.getData();
        })
    }
    rowClick = (record)=> {
        let data = {id:record.id}
        postAction("/bigdata/alarmHolidayWhitelist/findById", data).then(
            (res) => {
                if (res.success) {
                    this.setState({
                        showDetail: true,
                        detailInfo:Object.assign({},res.obj,record)
                    })
                } else {
                    message.warn(res.obj);
                }
            }
        )
    }
    changeFilter = (collegeCode)=> {
        this.setState({collegeCode},()=>this.getData())
    }
    render() {
        const dataSource = this.state.dataLists || [];
        dataSource.forEach((item,index)=>{
            dataSource[index].key = index;
            dataSource[index].index = index + 1;
        })
        return (
            <Card 
                className={this.props.className}
                title={(
                    <Row>
                        
                        <Col span={12} style={{textAlign:'left'}}>
                            <Button icon={'usergroup-add'} type="primary" size="large" onClick={()=>{
                                this.props.history.push('/mainframe/systemsetup/earlywarningwhitelist/holiday/addoredit')
                            }}>添 加</Button>
                        </Col>
                    </Row>
                )}>
                <div style={{marginBottom:24}}>
                    选择院系：
                    <Select className="mr-10" style={{width:178}}
                        placeholder="选择学院"
                        value={this.state.collegeCode}
                        onChange={(value) => { this.changeFilter(value) }}>
                        {this.state.collegeLists.map((item, index) => <Option key={index} value={item.collegeCode}>{item.collegeName}</Option>)}
                    </Select>
                </div>
                <Table dataSource={dataSource} 
                    rowClassName="clickRow"
                    columns={this.state.columns}
                    pagination={{
                        size: "small",
                        current:this.state.pageNum,
                        total:this.state.total,
                        showQuickJumper: true,
                        showTotal: total => `共 ${total} 条数据`
                    }}
                    size="middle"
                    bordered
                    onChange={this.changePage}
                    onRow={(record, index) => {
                        return {
                            onClick: () => this.rowClick(record, index),// 点击行
                        };
                    }} />
                {this.state.showDetail?(
                    <MainModal
                        title={'查看详情'}
                        visible={this.state.showDetail}
                        onCancel={()=>this.setState({showDetail:false})}
                        width={656}
                        noFooter={true}
                    >
                        <div className="ewc-detailModel">
                            <Row><Col span={8}>关联的预警策略</Col><Col span={16}>{this.state.detailInfo.strategyName}</Col></Row>
                            <Row style={{height:102}}>
                                <Col span={8}><Row type="flex" align="middle" justify="center">不预警对象</Row></Col>
                                <Col span={16}>
                                    <Row type="flex" align="middle" style={{overflowY:'auto'}}>
                                        {this.state.detailInfo.strategyTargetCollege}
                                    </Row>
                                </Col>
                            </Row>
                            <Row style={{height:132}}>
                                <Col span={8}>
                                    <Row type="flex" align="middle" justify="center">不预警时间</Row>
                                </Col>
                                <Col span={16}>
                                    <Row type="flex" align="middle" style={{overflowY:'auto'}}>
                                        {this.state.detailInfo.strategyDate.split(',').map((item,i)=>(<Col key={i} span={24}>{item}</Col>))}
                                    </Row>
                                </Col>
                            </Row>
                            <Row style={{height:102}}>
                                <Col span={8}><Row type="flex" align="middle" justify="center">备注</Row></Col>
                                <Col span={16}>
                                    <Row type="flex" align="middle" style={{overflowY:'auto'}}>
                                        {this.state.detailInfo.remark}
                                    </Row>
                                </Col>
                            </Row>
                        </div>
                    </MainModal>
                ):null}
            </Card>
        )
    }
}

export default withRouter(Holiday)


