import React, { Component } from 'react';
import { Tabs } from 'antd';
import Personal from './Personal/Personal'
import Holiday from './Holiday/Holiday'
import './whitelist.less'
import { postAction } from "../../../../axios"
import { getLocal } from "../../../../utils"
const TabPane = Tabs.TabPane;
class WhiteListConfig extends Component {
    state = {
        mode: 'person',
        tabs: []
    }
    componentWillMount() {
        let mode = window.localStorage.getItem('whitelistTab');
        if (mode) {
            this.setState({ mode });
        }
    }
    handleModeChange = (mode) => {
        window.localStorage.setItem('whitelistTab', mode);
        this.setState({
            mode
        })
    }
    componentDidMount() {
        const userinfo = JSON.parse(getLocal('userInfo'))
        let uid = userinfo.id
        let data = {
            sourceCode: "pc",
            menuId: 52,
            userId: uid

        }
        postAction('/bigdata/system/menu/labelList', data, 0, 1).then(res => {
            if (res.success) {
                this.setState({
                    tabs: res.obj
                })
            }
        })
    }
    getTabs = id => {
        switch (id) {
            case 521:
                return <Personal></Personal>
            case 522:
                return <Holiday></Holiday>
            default:
                return null
        }
    }
    render() {
        const { tabs } = this.state
        return (
            <React.Fragment>
                <div className='white-tab'>
                    <Tabs defaulActiveKey={521} onChange={this.callback} className="main-tabs">
                        {
                            tabs.map(item => (
                                <TabPane tab={item.name} key={item.id}>
                                    {this.getTabs(item.id)}
                                </TabPane>
                            ))
                        }
                    </Tabs>
                </div>

            </React.Fragment>
        )
    }
}

export default WhiteListConfig;


